body {
	font: 'Raleway', sans-serif;
  color: rgba(0,0,0,.5);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .5em;
  top: 15%;
}

h1 {
  font: 600 3.5em 'Raleway', sans-serif;
  color: rgba(0,0,0,.5);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .5em;
  width: 100%;
}

h3 {
	font: 900 1em 'Raleway', sans-serif;
  color: rgba(0,0,0,.5);
  text-align: center;
  text-transform: none;
  letter-spacing: 0.01em;
}

#color1, #color2{
  margin: 10px;
}

button {
  background-color:#dcdcdc;
 border-radius: 28px;
  border: none;
  outline: none;
	display: inline-block;
	cursor: pointer;
	color:#545454;
	font-size: 17px;
	font-weight: bold;
	padding: 12px 21px;
	text-decoration: none;
}


